import React, { useRef, useState, useEffect, useContext } from 'react';
import PropTypes from 'prop-types';
import MenuTrigger from 'components/atoms/MenuTrigger/MenuTrigger';
import {
  MenuWrapper,
  MenuWrapperBack,
  CloseMenuButton,
  MenuList,
  MenuLink,
  LoginLink,
  LoginLinkArrow,
} from './Menu.styles';
import {
  menuOnAnimation,
  menuOffAnimation,
} from 'assets/animations/animations';
import { ReactComponent as CloseIcon } from 'assets/icons/closeIcon.svg';
import { ReactComponent as LoginIcon } from 'assets/icons/loginIcon.svg';
import { ReactComponent as AvatarIcon } from 'assets/icons/avatarIcon.svg';
import { ReactComponent as SettingsIcon } from 'assets/icons/settingsIcon.svg';
import { ReactComponent as HomeIcon } from 'assets/icons/homeIcon.svg';
import { NavLink } from 'react-router-dom';
import { logout } from 'helpers/auth.service';
import { AppContext } from 'store/Store';

const Menu = ({ openLoginScreen }) => {
  let menuWrapperBack = useRef(null);
  let menuWrapper = useRef(null);
  let menuCloseButton = useRef(null);
  const { currentUser, setCurrentUser } = useContext(AppContext);

  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const changeMenuButtonPosition = () =>
    setIsMenuOpen((prevState) => !prevState);

  const menuOn = () => {
    changeMenuButtonPosition();
    menuOnAnimation(menuWrapperBack, menuWrapper, menuCloseButton);
  };

  const menuOff = () => {
    changeMenuButtonPosition();
    menuOffAnimation(menuCloseButton, menuWrapper, menuWrapperBack);
  };

  const goLoginHandler = () => {
    openLoginScreen();
    menuOff();
  };

  const logOut = () => {
    logout();
    setCurrentUser(null);
  };

  return (
    <>
      <MenuTrigger isMenuOpen={isMenuOpen} handler={menuOn} />
      <MenuWrapperBack ref={(el) => (menuWrapperBack = el)}>
        <MenuWrapper ref={(el) => (menuWrapper = el)}>
          <LoginLink>
            <AvatarIcon />
            {currentUser ? currentUser.username : 'Niezalogowany'}
            <LoginLinkArrow />
          </LoginLink>
          <MenuList>
            <NavLink to="/home" onClick={menuOff}>
              <MenuLink>
                <HomeIcon />
                Strona główna
              </MenuLink>
            </NavLink>
            <NavLink to="/dashboard" onClick={menuOff}>
              <MenuLink>
                <SettingsIcon />
                Dashboard
              </MenuLink>
            </NavLink>
            <NavLink to="/documents" onClick={menuOff}>
              <MenuLink>
                <SettingsIcon />
                Dokumenty
              </MenuLink>
            </NavLink>
            <NavLink to="/aplication" onClick={menuOff}>
              <MenuLink>
                <SettingsIcon />
                Aplikacja
              </MenuLink>
            </NavLink>
            <NavLink to="/userSettings" onClick={menuOff}>
              <MenuLink>
                <SettingsIcon />
                Moje konto
              </MenuLink>
            </NavLink>
          </MenuList>
          {currentUser ? (
            <LoginLink onClick={logOut}>
              <LoginIcon />
              Wyloguj się
              <LoginLinkArrow />
            </LoginLink>
          ) : (
            <LoginLink onClick={goLoginHandler}>
              <LoginIcon />
              Zaloguj się
              <LoginLinkArrow />
            </LoginLink>
          )}
        </MenuWrapper>
        <CloseMenuButton ref={(el) => (menuCloseButton = el)} onClick={menuOff}>
          <CloseIcon />
        </CloseMenuButton>
      </MenuWrapperBack>
    </>
  );
};

Menu.propTypes = {
  openLoginScreen: PropTypes.func,
};

export default Menu;
