import React, { useState } from 'react';
import { ThemeProvider } from 'styled-components';
import { theme } from 'assets/styles/theme';
import { GlobalStyle } from 'assets/styles/GlobalStyle';
import { StyledRoot } from './Root.styles';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import LoginAndRegister from 'components/organisms/LoginAndRegister/LoginAndRegister';
import Menu from 'components/organisms/Menu/Menu';
import Store from 'store/Store';
import Home from 'components/pages/Home/Home';
import UserSettings from 'components/pages/UserSettings/UserSettings';

const Root = () => {
  const [isLoginOpen, setIsLoginOpen] = useState(false);

  const openHandle = () => setIsLoginOpen(true);
  const closeHandle = () => setIsLoginOpen(false);
  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <Store>
        <Router>
          <StyledRoot>
            <Menu openLoginScreen={openHandle} />
            <Switch>
              <Route path={['.', '/home']} component={Home} />
              <Route path="/userSettings" component={UserSettings} />
            </Switch>
            {isLoginOpen && <LoginAndRegister handler={closeHandle} />}
          </StyledRoot>
        </Router>
      </Store>
    </ThemeProvider>
  );
};

export default Root;
