import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { AppContext } from 'store/Store';
import { UserSettingsWrapper } from './UserSettings.styles';

const UserSettings = (props) => {
  const { currentUser } = useContext(AppContext);

  return (
    <UserSettingsWrapper>
      {currentUser ? (
        <div>
          <p>Nazwa użytkownika: {currentUser && currentUser.username}</p>
          <p>id: {currentUser && currentUser.id}</p>
          <p>Token: {currentUser && currentUser.accessToken.slice(0, 9)}****</p>
          <p>rola: {currentUser && currentUser.role}</p>
        </div>
      ) : (
        <p>Nie jesteś zalogowany</p>
      )}
    </UserSettingsWrapper>
  );
};

UserSettings.propTypes = {};

export default UserSettings;
