import React, { useState, useRef, useEffect, useContext } from 'react';
import PropTypes from 'prop-types';
import { LoginDocumentsAnimation } from 'assets/animations/animations';
import {
  StyledLoginWrapper,
  Header,
  NavWrapper,
  NavItem,
  StyledSpan,
  LoginBox,
  LoginImageWrapper,
  ImageWrapper,
} from './LoginAndRegister.styles';
import { ReactComponent as LoginImage } from 'assets/images/loginImage.svg';
import LoginForm from 'components/molecules/LoginForm/LoginForm';
import RegisterForm from 'components/molecules/RegisterForm/RegisterForm';
import ExitButton from 'components/atoms/ExitButton/ExitButton';
import ArrowButton from 'components/atoms/ArrowButton/ArrowButton';
import { AppContext } from 'store/Store';

const LoginAndRegister = ({ handler }) => {
  const { clearFormInputs } = useContext(AppContext);

  const imageRef = useRef(null);
  let component = useRef(null);

  const [isSignIn, setIsSignIn] = useState(true);

  const goSignIn = () => {
    clearFormInputs();
    setIsSignIn(true);
  };
  const goSignUp = () => {
    clearFormInputs();
    setIsSignIn(false);
  };

  // >>>>>>>>>>>>>> LOGIN AND REGISTRATION LOGIC <<<<<<<<<<<<<<<

  useEffect(() => {
    const [elements] = imageRef.current.children;

    const firstDocument = elements.querySelector('#documentOne');
    const secondDocument = elements.querySelector('#documentTwo');
    const thirdDocument = elements.querySelector('#documentThree');
    const fourthDocument = elements.querySelector('#documentFour');
    LoginDocumentsAnimation(
      firstDocument,
      secondDocument,
      thirdDocument,
      fourthDocument,
      component
    );
  }, []);

  return (
    <LoginBox ref={(el) => (component = el)}>
      <ExitButton handler={handler} />

      {/* >>>>>>>>>>>>>>>>>>>>> IMAGE SVG AREA <<<<<<<<<<<<<<<<<<<<< */}

      <LoginImageWrapper>
        {isSignIn ? (
          <ArrowButton handler={goSignUp}>Zarejestruj się</ArrowButton>
        ) : (
          <ArrowButton handler={goSignIn}>Zaloguj się</ArrowButton>
        )}
        <ImageWrapper ref={imageRef}>
          <LoginImage />
        </ImageWrapper>
      </LoginImageWrapper>

      {/* >>>>>>>>>>>>>>>>>>>>> FORMS AREA <<<<<<<<<<<<<<<<<<<<< */}

      <StyledLoginWrapper>
        <Header>Witaj!</Header>
        <NavWrapper>
          <NavItem active={isSignIn} onClick={goSignIn}>
            SIGN IN
          </NavItem>
          <NavItem active={!isSignIn} onClick={goSignUp}>
            SIGN UP
          </NavItem>
        </NavWrapper>
        {isSignIn ? <LoginForm closeLoginBox={handler} /> : <RegisterForm closeLoginBox={handler} />}
        <StyledSpan>Zapomniałeś hasła?</StyledSpan>
      </StyledLoginWrapper>
    </LoginBox>
  );
};

LoginAndRegister.propTypes = {
  handler: PropTypes.func,
};

export default LoginAndRegister;
